*{
  padding: 0;
  margin: 0;
}

.App{
  display: -ms-flexbox;
  display: flex;
  padding: 5%;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  position: absolute;
}
.Grid{
  width: 420px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 400px;
}
.color{
  position: absolute;
  top: 0;
  left: 0;
}
.container{
  height: 400px;
  width: 110px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-left: 20px;
}

.block_container{
  -webkit-transition: -webkit-transform .1s ease;
  transition: -webkit-transform .1s ease;
  -o-transition: transform .1s ease;
  transition: transform .1s ease;
  transition: transform .1s ease, -webkit-transform .1s ease;
  -webkit-transform-origin:100% 100%;
      -ms-transform-origin:100% 100%;
          transform-origin:100% 100%;/*鼠标的位置在3/3的位置*/
  margin-top: 20px;
  height: 108px;
  width: 110px;
}

.score{
  font-size: 20px;
  text-align: center;
  width: 100%;
  position: fixed;
  z-index: 99999;
  top: 10px;
}

.block_cell{
  width: 20px;
  height: 20px;
  margin-right: 2px;
  border-radius: 2px;
  display: inline-block;
}

.cell{
  width: 9.2%;
  height: 9.2%;
  background: #eee;
  -webkit-transition: background-color .3s ease;
  -o-transition: background-color .3s ease;
  transition: background-color .3s ease;
  display: inline-block;
  padding: 0;
  margin: 2px 2px 0 0;
  border-radius: 2px;
}
.dispear{
  -webkit-animation: smaller .3s 1;
          animation: smaller .3s 1
}
@-webkit-keyframes smaller{
  from{
    -webkit-transform: scale(1);
            transform: scale(1)
  }
  to{
    -webkit-transform: scale(0);
            transform: scale(0)
  }
}
@keyframes smaller{
  from{
    -webkit-transform: scale(1);
            transform: scale(1)
  }
  to{
    -webkit-transform: scale(0);
            transform: scale(0)
  }
}
.color .cell{
  z-index: 10;
  background-color: transparent;
}
.newGame{
  padding: 3px;
  font-weight: 600;
  border-radius: 4px;
  margin-left: 20px;
  color: white;
  background-color: #eda;
  cursor: pointer;
}
.newGame:hover{
  -webkit-box-shadow: #ddd 0 3px 5px 3px;
          box-shadow: #ddd 0 3px 5px 3px;
}
@media screen and (max-width:420px){
  .App{
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .container{
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    height: 110px
  }
}


